___CSS_LOADER_EXPORT___.push([module.id, ".google-map{height:800px;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/frontend/components/GoogleMap.vue"],"names":[],"mappings":"AAiEE,YAEE,YAAa,CADb,UAEF","sourcesContent":["<template>\r\n    <div ref=\"map\" class=\"google-map\"></div>\r\n  </template>\r\n  \r\n  <script>\r\n  import { Loader } from '@googlemaps/js-api-loader';\r\n  \r\n  export default {\r\n    name: 'GoogleMap',\r\n    props: {\r\n      apiKey: String,\r\n      mapOptions: Object,\r\n      mapStyles: Object,\r\n      markers: Array\r\n    },\r\n    data() {\r\n      return {\r\n        map: null,\r\n        infoWindow: null,\r\n      };\r\n    },\r\n    async mounted() {\r\n      await this.initMap();\r\n    },\r\n    methods: {\r\n      async initMap() {\r\n        const loader = new Loader({\r\n          apiKey: this.apiKey,\r\n          version: 'weekly',\r\n        });\r\n  \r\n        try {\r\n          await loader.load();\r\n          this.map = new google.maps.Map(this.$refs.map, {\r\n            ...this.mapOptions,\r\n            styles: this.mapStyles,\r\n          });\r\n          this.infoWindow = new google.maps.InfoWindow();\r\n          this.createMarkers();\r\n        } catch (error) {\r\n          console.error('Error loading Google Maps API:', error);\r\n        }\r\n      },\r\n      createMarkers() {\r\n        if (this.markers && this.markers.length > 0) {\r\n          this.markers.forEach((marker) => {\r\n            const gMarker = new google.maps.Marker({\r\n              position: marker.position,\r\n              map: this.map,\r\n              title: marker.title,\r\n            });\r\n\r\n            gMarker.addListener('click', () => {\r\n              this.infoWindow.close(); // Close any previously opened info window\r\n              this.infoWindow.setContent(marker.title);\r\n              this.infoWindow.open(this.map, gMarker);\r\n            });\r\n          });\r\n        }\r\n      },\r\n    },\r\n  };\r\n  </script>\r\n  \r\n  <style>\r\n  .google-map {\r\n    width: 100%;\r\n    height: 800px;\r\n  }\r\n  </style>\r\n  "],"sourceRoot":""}]);
